import { createRouter, createWebHistory } from 'vue-router';

import LoginView from '../views/account/Login-View.vue'; // 
import RegisterView from '../views/account/Register-View.vue'; // 
import ProfileView from '../views/account/Profile-View.vue'; // 

import IndexView from '../views/Index-View.vue'; // 
import NotFoundView from '../views/NotFound-View.vue'; // 

import PyvisView from '../views/Pyvis-View.vue'; // Pyvis

import FoldersListView from '../views/folders/List-View.vue'; // 

import ChunksListView from '../views/chunks/List-View.vue'; // 
import ChunksListAddView from '../views/chunks/Add-View.vue'; // 
import ChunksListEditView from '../views/chunks/Edit-View.vue'; // 

import ChunkSentencesListView from '../views/chunk_sentences/List-View.vue'; // 
import ChunkSentencesListCreateView from '../views/chunk_sentences/Create-View.vue'; // 
import ChunkSentencesListEditView from '../views/chunk_sentences/Edit-View.vue'; // 

import SpeakMoreSelectView from '../views/speak_more/Select-View.vue'; // 
import SpeakMorePracticeView from '../views/speak_more/Practice-View.vue'; // 
import SpeakMoreResultView from '../views/speak_more/Result-View.vue';  // 

import StatisticsView from '../views/Statistics-View.vue'; // 

import UsageView from '../views/Usage-View.vue'; // 

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { name: 'Login', path: '/account/login', component: LoginView, meta:{title: ''}}, // 
    { name: 'Register', path: '/account/register', component: RegisterView, meta:{title: ''}}, // 
    { name: 'Profile', path: '/account/profile', component: ProfileView, meta:{title: ''}}, // 

    { name: 'Index', path: '/', component: IndexView, meta:{title: ''}}, // 
    { name: 'NotFound', path: '/:catchAll(.*)', component: NotFoundView, meta:{title: ''}}, // 

    { name: 'Pyvis', path: '/pyvis', component: PyvisView, meta:{title: ''}}, // Pyvis

    { name: 'Folders-List', path: '/folders/list', component: FoldersListView, meta:{title: ''}}, // 

    { name: 'Chunks-List', path: '/chunks/list', component: ChunksListView, meta:{title: ''}}, // 
    { name: 'Chunks-List-Add', path: '/chunks/list/add', component: ChunksListAddView, meta:{title: ''}}, // 
    { name: 'Chunks-List-Edit', path: '/chunks/list/edit', component: ChunksListEditView, meta:{title: ''}}, // 

    { name: 'Chunk-Sentences-List', path: '/chunk_sentences/list', component: ChunkSentencesListView, meta:{title: '/'}}, // /
    { name: 'Chunk-Sentences-List-Create', path: '/chunk_sentences/list/create', component: ChunkSentencesListCreateView, meta:{title: '/'}}, // /
    { name: 'Chunk-Sentences-List-Edit', path: '/chunk_sentences/list/edit', component: ChunkSentencesListEditView, meta:{title: '/'}}, // /

    { name: 'Speak-More-Select', path: '/speak_more/select', component: SpeakMoreSelectView, meta:{title: ''}},
    { name: 'Speak-More-Practice', path: '/speak_more/practice', component: SpeakMorePracticeView, meta:{title: ''}},
    { name: 'Speak-More-Result', path: '/speak_more/result', component: SpeakMoreResultView, meta:{title: ''}},
    
    { name: 'Statistics', path: '/statistics', component: StatisticsView, meta:{title: ''}}, // 

    { name: 'Usage', path: '/usage', component: UsageView, meta:{title: ''}}, // 
  ],
});

export default router;
