<template>
  <v-app :theme="theme">
    <!--  () -->
    <v-app-bar color="primary">
      <!--  -->
      <template v-slot:prepend>
        <!--  -->
        <v-app-bar-nav-icon @click.stop="drawer = !drawer">
        </v-app-bar-nav-icon>
      </template>
      <v-app-bar-title>Chunk Hub</v-app-bar-title>

      <!--  -->
      <template v-slot:append>
        <v-spacer></v-spacer>
        <v-avatar v-if="isLoggedIn" @click="goToProfile" style="cursor: pointer;">
          <!-- <img :src="avatarUrl" alt="Avatar"> -->
          <img src="@/assets/logo.png" alt="avatarImage" class="avatar-image" />
        </v-avatar>
      </template>
    </v-app-bar>

    <!--  -->
    <v-main>
      <router-view />
      <!-- router  -->
    </v-main>

    <!--  ( v-main  height ) -->
    <!-- app  -->
    <v-footer color="primary" app height="30" class="d-flex justify-center">
      <span class="white--text">&copy; 2024 Chunk Hub</span>
    </v-footer>

    <!--  -->
    <v-navigation-drawer v-model="drawer" temporary>
      <v-list nav>
        <!--  -->
        <template v-if="!isLoggedIn">
          <v-list-item title="" value="register" to="/account/register"></v-list-item>
          <v-list-item title="" value="login" to="/account/login"></v-list-item>
        </template>

        <!--  -->
        <template v-else>
          <!--  -->
          <v-list-item title="" value="home" to="/" :prepend-icon="mdiHome"></v-list-item>

          <!--  -->
          <!-- <v-list-group value="Features">
            <template v-slot:activator="{ props }">
              <v-list-item v-bind="props" title="" :prepend-icon="mdiTranslateVariant"></v-list-item>
            </template> -->
            <v-list-item title="" value="chunk_mng" to="/folders/list"
              :prepend-icon="mdiBookshelf"></v-list-item>
            <v-list-item title="" value="speak_more" to="/speak_more/select"
              :prepend-icon="mdiAccountVoice"></v-list-item>
          <!-- </v-list-group> -->

          <!--  -->
          <v-list-group value="Analytics">
            <template v-slot:activator="{ props }">
              <v-list-item v-bind="props" title="" :prepend-icon="mdiGoogleAnalytics"></v-list-item>
            </template>
            <v-list-item title="" value="chunk_network" to="/pyvis"
              :prepend-icon="mdiHubOutline"></v-list-item>
            <v-list-item title="" value="Statistics" to="/statistics" :prepend-icon="mdiSchool"></v-list-item>
          </v-list-group>

          <!--  -->
          <v-list-item title="" value="usage" to="/usage" :prepend-icon="mdiHelp"></v-list-item>

          <!--  -->
          <v-list-item title="" value="logout" @click="showLogoutDialog" :prepend-icon="mdiLogout"></v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>

    <!--  -->
    <v-dialog v-model="logoutDialog" max-width="290">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text></v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="logoutDialog = false"></v-btn>
          <v-btn color="blue darken-1" text @click="logout">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script setup>
import axios from 'axios';
import { ref, watch } from "vue";
import { useRoute } from "vue-router";

// 
//  theme  light  dark  
const theme = ref("dark");
const drawer = ref(false);
const logoutDialog = ref(false);
const isLoggedIn = ref(false);
// const avatarUrl = ref('/path/to/default/avatar.jpg'); // 
const route = useRoute();

// const pageTitle = computed(() => {
//   return route.meta.title || '';
// });

watch(route, (newRoute) => {
  document.title = newRoute.meta.title || '';
});

function showLogoutDialog() {
  logoutDialog.value = true;
}

function logout() {
  logoutDialog.value = false;
  // API
  // /logoutGET
  fetch("/account/logout", {
    method: "GET",
  })
    .then((response) => {
      // 
      if (response.ok) {
        window.location.href = "/account/login"; // 
      } else {
        // 
        console.error("");
      }
    })
    .catch((error) => {
      console.error("", error);
    });
}

function goToProfile() {
  window.location.href = '/account/profile';
}

function checkSession() {
  axios.get('/account/check_session')
    .then(response => {
      isLoggedIn.value = response.data.logged_in;
      // if (isLoggedIn.value) {
      //   avatarUrl.value = response.data.avatar_url; // URL
      // }
      // console.log('Session checked:', response.data);
    })
    .catch(error => {
      console.error('Error checking session:', error);
    });
}

checkSession();
</script>
